import React from "react";

import Header from "./Header";
import Footer from "./Footer";
import { Outlet } from "react-router-dom";

function Main({isUserState , tokenUtill ,modifyMode ,setModifyMode , adminMode}) {
    return (
        <>
            <Header isUserState={isUserState} tokenUtill={tokenUtill} modifyMode={modifyMode} setModifyMode = {setModifyMode} adminMode = {adminMode}/>

            <Outlet />

            <Footer />
        </>
    );
};

export default Main;
