import { useState, useRef } from "react";
import "./LoginModal.scss";
import $ from "jquery";
import { Dialog } from "primereact/dialog";

function Modal({ LModal, setLModal, tokenUtill }) {
	const iDRef = useRef(null);
	const passwordRef = useRef(null);
	const [isID, setID] = useState("");
	const [isPassword, setPassword] = useState("");
	const [isWorngText, setWorngText] = useState(" ");

	async function userSubmit(e) {
		let uEmail = isID;
		let uPass = isPassword;
		if (!uEmail) {
			iDRef.current.focus()
			setWorngText("  ");
			return false;
		}
		if (!uPass) {
			passwordRef.current.focus()
			setWorngText("  ");
			return false;
		}

		if (await tokenUtill.userLogIn(uEmail, uPass)) {
			setLModal(false);
		} else {
			iDRef.current.focus()
			setWorngText("    .");
		}
	}
	const handleOnKeyPress = (e) => {
		if (e.key === "Enter") {
			userSubmit(); // Enter     
		}
	};

	return (
		<Dialog
			className="login-modal"
			visible={LModal}
			position={"top"}
			style={{ width: "400px" }}
			onHide={() => setLModal(false)}
			// footer={footerContent}
			draggable={false}
			resizable={false}>
			<div className="modal-content">
				<div className="login-text">  </div>
				<div className="tab-content">
					<p className="worng-text">{isWorngText}</p>
					<div className="filed LogIn-area">
						<div className="field-wrap">
							<input
								ref={iDRef}
								className="u-email"
								type="text"
								required="required"
								onChange={function (e) {
									setID(e.target.value);
									setWorngText("");
								}}
								onKeyPress={handleOnKeyPress}
								autoComplete="off"
							/>
							<label>ID</label>
						</div>
						<div className="field-wrap">
							<input
								ref={passwordRef}
								className="u-pass"
								type="password"
								required="required"
								onChange={function (e) {
									setPassword(e.target.value);
									setWorngText("");
								}}
								onKeyPress={handleOnKeyPress}
								autoComplete="off"
							/>
							<label>Password</label>
						</div>
					</div>
				</div>
				<button type="submit" data-target="LogIn" className="button submit-button" onClick={userSubmit}>
					LogIn
				</button>
			</div>
		</Dialog>
	);
}

export default Modal;
