import React from "react";
import { Button } from "primereact/button";
import { useEffect, useState } from "react";

function DeleteButton({ callback, className }) {
	const [istoggle, settoggle] = useState(true);

	function runCallback() {
		callback();
	}
	if (istoggle) {
		return (
			<div className={className}>
				<Button
					severity="danger"
					rounded
					text
					icon="pi pi-trash"
					onClick={function () {
						settoggle(false);
					}}></Button>
			</div>
		);
	} else {
		return (
			<div className={className}>
				<Button icon="pi pi-check" rounded text onClick={runCallback}></Button>
				<Button
					icon="pi pi-times"
					severity="danger"
					rounded
					text
					onClick={function () {
						settoggle(true);
					}}></Button>
			</div>
		);
	}
}

export default DeleteButton;
