import React, { useRef, useEffect, useState } from "react";

import "./Home.scss";
import $ from "jquery";
import { Link } from "react-router-dom";
import { Swiper, SwiperSlide } from "swiper/react";
import { Grid } from "swiper/modules";
import "swiper/css";
import "swiper/css/grid";
import { DataTable } from "primereact/datatable";
import { Column } from "primereact/column";
import { Dropdown } from "primereact/dropdown";
import { InputText } from "primereact/inputtext";
import { Button } from "primereact/button";
import DeleteButton from "./utils/DeleteButton";
import CustomInput from "./utils/customInput";
import CustomToggle from "./utils/customToggle";

import "primeicons/primeicons.css";
import "primereact/resources/themes/lara-light-indigo/theme.css"; // theme
import "primereact/resources/primereact.css";

function Home({ modifyMode, adminMode }) {
	const [solutions, setsolutions] = useState([]);
	const swiperRef = useRef([]);
	const imageFileRef = useRef({});
	function moveSlide(i, slideNum, e) {
		let swiper = swiperRef.current[i];
		let index = swiper.activeIndex;
		let moveCount = parseInt(slideNum) * parseInt(1);
		swiper.slideTo(index + moveCount);
	}

	function lendersHome() {
		if (modifyMode && adminMode) {
			return modifySolutionsTable();
		} else {
			return solutionsSwiper();
		}
	}

	let solutionsType = [
		{ value: "grid", label: "grid" },
		{ value: "normal", label: "normal" },
	];
	function typechange(e, i) {
		let newsol = [...solutions];
		newsol[i].type = e.value;
		// solutions[i].type = e.value;
		setsolutions(newsol);
	}

	function projectLocationChange(e, i) {
		let newsol = [...solutions];
		newsol[i].project = e.value;

		// solutions[i].type = e.value;
		setsolutions(newsol);
	
		console.log(e);
		let query  = 'mutation{fetchProjectLocation(solutionId:'+solutions[i].id+' ,dragIndex:'+e.dragIndex+' ,dropIndex:'+e.dropIndex+'  )}';
		
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);
		ajaxQueryRequest(formData);
	}

	function solutionLocationChange(i, flag) {
		let newsol = [...solutions];
		if (flag) {
			if (i > 0) {
				[newsol[i], newsol[i - 1]] = [newsol[i - 1], newsol[i]];
			}
		} else {
			if (i < newsol.length - 2) {
				[newsol[i], newsol[i + 1]] = [newsol[i + 1], newsol[i]];
			
			}
		}
		setsolutions(newsol);
		let query  = 'mutation{fetchSolutionLocation(solIndex:'+i+' , flag:'+flag+')}';
		
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);
		ajaxQueryRequest(formData);
	}

	function newProject(i) {
		// let newsol = [...solutions];
		let query = "mutation{newProject(solutionId:" + solutions[i].id + "){ id title image link} }";
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);

		let callback = function (data) {
			let newsol = [...solutions];
			newsol[i].project.push(data.newProject);
			setsolutions(newsol);
		};
		ajaxQueryRequest(formData, callback);
		// newsol[i].solution.push({ id: "9", title: "Title", image: "/Solution/.jpg", link: "/" });
	}
	function deleteSolution(i) {
		let query = "mutation{deleteSolution(solutionId:" + solutions[i].id + ")}";
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);

		let callback = function (data) {
			let newsol = [...solutions];
			newsol.splice(i, 1);
			setsolutions(newsol);
		};
		ajaxQueryRequest(formData, callback);
	}
	function newSolution() {
		let query = "mutation{ newSolution{id, name , type , project { id , title , image , link }}}";
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);

		let callback = function (data) {
			let newsol = [...solutions];
			newsol.push(data.newSolution);
			setsolutions(newsol);
		};
		ajaxQueryRequest(formData, callback);
		// newsol[i].solution.push({ id: "9", title: "Title", image: "/Solution/.jpg", link: "/" });
	}
	function solutionTitleChange(e, i) {
		let query = "mutation{ fetchSoultionName(solutionId:" + solutions[i].id + ', name:"' + e.target.value + '")}';
		let formData = new FormData();
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);

		let callback = function (data) {
			let newsol = [...solutions];
			newsol[i].name = e.target.value;
			setsolutions(newsol);
		};
		console.log(query);
		ajaxQueryRequest(formData, callback);
	}
	function ModifyProject(e, i) {
		let projectId = solutions[i].project[e.index].id;
		console.log(solutions);
		let projectFormData = new FormData();
		projectFormData.append(
			"operations",
			JSON.stringify({
				query:
					"mutation ($imageFile: Upload) {fetchProject(solutionId:" +
					solutions[i].id +
					" , projectId:" +
					projectId +
					' ,title:"' +
					e.newData.title +
					'" , imageFile:$imageFile ,imgUrl:"' +
					e.newData.image +
					'", link:"' +
					e.newData.link +
					'"){id title image link}}',
			})
		);
		projectFormData.append("imageFile", imageFileRef.current[projectId]);
		let filemap = {};
		filemap.imageFile = ["variables.imageFile"];
		projectFormData.append("map", JSON.stringify(filemap));
		let callback = function (data) {
			let newsol = [...solutions];
			newsol[i].project[e.index] = data.fetchProject;
			setsolutions(newsol);
		};

		ajaxQueryRequest(projectFormData, callback);
	}
	const imageTemp = (data) => {
		return (
			<div className="project-img-area">
				<img src={data.image} />
			</div>
		);
	};
	const linkTemp = (data) => {
		return (
			<Link className="swiper-slide-cont " to={data.link}>
				{data.link}
			</Link>
		);
	};

	const imageEditor = (options) => {
		let input = document.createElement("input");
		input.type = "file";

		input.onchange = function (e) {
			var reader = new FileReader();
			reader.onload = function (e) {
				imageFileRef.current[options.rowData.id] = file;
				options.editorCallback(e.target.result);
			};
			let file = input.files[0];
			reader.readAsDataURL(file);
		};

		function fileDrop(e) {
			e.preventDefault();
			let file = e.dataTransfer.files[0];

			var reader = new FileReader();
			reader.onload = function (e) {
				imageFileRef.current[options.rowData.id] = file;
				options.editorCallback(e.target.result);
			};
			reader.readAsDataURL(file);
		}

		function imageClick(e) {
			input.click();
		}
		return (
			<div onDrop={fileDrop} onClick={imageClick} className="project-img-area modifyMode">
				<img src={options.value} />
			</div>
		);
	};
	const textEditor = (options) => {
		return (
			<InputText
				type="text"
				value={options.value}
				onChange={function (e) {
					options.editorCallback(e.target.value);
				}}
			/>
		);
	};
	const linkEditor = (options) => {
		return (
			<InputText
				type="text"
				value={options.rowData.link}
				onChange={function (e) {
					options.editorCallback(e.target.value);
				}}
			/>
		);
	};

	function modifySolutionsTable() {
		let result = [];

		for (let i = 0; i < solutions.length; i++) {
			let dom = (
				<>
					<div className="solution-location-change-area hover-button">
						<Button
							icon="pi pi-caret-up"
							style={i == 0 ? { opacity: 0, pointerEvents: "none" } : { opacity: "1" }}
							onClick={function () {
								solutionLocationChange(i, true);
							}}></Button>
						<Button
							icon="pi pi-caret-down"
							style={i == solutions.length - 1 ? { opacity: 0, pointerEvents: "none" } : { opacity: "1" }}
							onClick={function () {
								solutionLocationChange(i, false);
							}}></Button>
					</div>
					<CustomToggle> </CustomToggle>

					<div className="swiper-area">
						<div className="swiper-title">
							<span>Name : </span>
							<CustomInput
								type="text"
								value={solutions[i].name}
								customOnCange={function (e) {
									solutionTitleChange(e, i);
								}}></CustomInput>
							<span>Type : </span>
							<Dropdown
								value={solutions[i].type}
								onChange={function (e) {
									typechange(e, i);
								}}
								options={solutionsType}
							/>
							<Button
								onClick={function () {
									newProject(i);
								}}
								className="add-project-btn">
								+PROJECT
							</Button>

							<DeleteButton
								severity="danger"
								callback={function () {
									deleteSolution(i);
								}}
								className="rm-solution-btn hover-button">
								Delete
							</DeleteButton>
						</div>
					</div>
				</>
			);
			function deleteArea(data, rowdata) {
				let index = rowdata.rowIndex;

				function deletepro() {
					let query = "mutation{deleteProject(solutionId:" + solutions[i].id + " , projectId:" + solutions[i].project[index].id + ")}";
					let formData = new FormData();
					formData.append(
						"operations",
						JSON.stringify({
							query: query,
						})
					);

					let callback = function () {
						let newsol = [...solutions];
						delete newsol[i].project[index];
						setsolutions([...newsol]);
					};

					ajaxQueryRequest(formData, callback);
				}

				return <DeleteButton callback={deletepro} />;
			}

			let childTable = (
				<div className="card">
					<DataTable
						value={solutions[i].project}
						reorderableColumns
						reorderableRows
						onRowReorder={function (e) {
							projectLocationChange(e, i);
						}}
						onRowEditComplete={function (e) {
							ModifyProject(e, i);
						}}
						editMode="row">
						<Column rowReorder style={{ width: "5%", textAlign: "center" }} />
						<Column field="title" style={{ width: "35%" }} editor={(options) => textEditor(options)} header="title"></Column>

						<Column field="link" style={{ width: "30%" }} body={linkTemp} editor={(options) => linkEditor(options)} header="LinkTo"></Column>
						<Column field="image" style={{ width: "10%" }} body={imageTemp} editor={(options) => imageEditor(options)} header="Image"></Column>

						<Column style={{ width: "10%", textAlign: "center" }} rowEditor bodyStyle={{ textAlign: "center" }}></Column>
						<Column style={{ width: "10%", textAlign: "center" }} body={deleteArea}></Column>
					</DataTable>
				</div>
			);
			result.push(
				<div key={solutions[i].id} className="modify-solution-area">
					{dom}
					{childTable}
				</div>
			);
		}

		result.push(
			<Button
				key={"add-solution-btn"}
				onClick={function () {
					newSolution();
				}}
				className="add-solution-btn">
				+SOLUTION
			</Button>
		);

		return result;
	}
	function solutionsSwiper() {
		let result = [];
		let slidesPerView = 5;
		for (let i = 0; i < solutions.length; i++) {
			let dom = (
				<div className="swiper-area">
					<div className="swiper-title">{solutions[i].name}</div>
				</div>
			);
			let swiper;

			let buttonarea = (
				<div className="swiper-button-area">
					<button
						onClick={function (e) {
							moveSlide(i, -slidesPerView);
						}}
						className="swiper-button-prev">
						<img src="/img/chevron_left.svg"></img>
					</button>
					<button
						onClick={function (e) {
							moveSlide(i, slidesPerView);
						}}
						className="swiper-button-next">
						<img src="/img/chevron_right.svg"></img>
					</button>
				</div>
			);

			if (solutions[i].type == "grid") {
				swiper = (
					<Swiper
						className="grid"
						grid={{
							rows: 2,
						}}
						spaceBetween={15}
						slidesPerView={slidesPerView}
						modules={[Grid]}
						onBeforeInit={(swiper) => (swiperRef.current[i] = swiper)}>
						{buttonarea}
						{swipersolution(solutions[i].project)}
					</Swiper>
				);
			} else {
				swiper = (
					<Swiper className="wide" spaceBetween={15} slidesPerView={5} onBeforeInit={(swiper) => (swiperRef.current[i] = swiper)}>
						{buttonarea}
						{swipersolution(solutions[i].project)}
					</Swiper>
				);
			}

			result.push(
				<div key={solutions[i].id} className="solution-area">
					{dom}
					{swiper}
				</div>
			);
		}
		return result;
	}

	function swipersolution(projects) {
		let result = [];
		for (let i = 0; i < projects.length; i++) {
			if (projects[i]) {
				let imgUrl = projects[i].image;

				result.push(
					<SwiperSlide key={projects[i].id}>
						<Link className="swiper-slide-cont " to={projects[i].link} style={{ backgroundImage: `url(${imgUrl})` }}>
							<div className="bg-gradient">
								<div className="slide-cont-title">{projects[i].title}</div>
							</div>
						</Link>
					</SwiperSlide>
				);
			}
		}
		return result;
	}

	function loadsolution() {
		let formData = new FormData();
		let query = "query{SolutionList { id name type project { id title image link}}}";
		formData.append(
			"operations",
			JSON.stringify({
				query: query,
			})
		);
		let callback = function (data) {
			setsolutions(data.SolutionList);
		};
		ajaxQueryRequest(formData, callback);
		//////
	}

	function patchSolution() {}

	function patchSolutions() {}

	function patchSolutionList() {}

	function ajaxQueryRequest(formData, callback) {
		$.ajax({
			type: "POST",
			url: "http://localhost:3000/graphql",
			data: formData,
			contentType: false,
			processData: false,
			success: function (result) {
				//   
				if (result.data) {
					if (callback) {
						callback(result.data);
					}
				} else {
					console.log(result);
				}
			},
			error: function (e) {
				console.log(e);
			},
		});
	}

	useEffect(function () {
		loadsolution();
	}, []);

	return (
		<div className="home-container">
			<span className="cursor"></span>
			<img className="bg-img" src="/img/bg-img/skyscrapers.jpg" />
			<div className="shape-container">
				<img src="/img/pluxitylogo_blue_edited_waifu2x_photo_noise1.png" />
				<span className="text">
					<p>D</p>igital <p>T</p>win <p>R</p>esearch <p>C</p>enter
				</span>
				<span className="desc">
					Digital-Twin based Smart City/Building <br></br>Untact Management System
				</span>
				<a className="contact-button" href="#">
					<p>CONTACT</p>
					<span></span>
				</a>
			</div>

			<div className="container-body">{lendersHome()}</div>
		</div>
	);
}

export default Home;

$(function () {
	$(".contact-button")
		.on("mouseenter", function (e) {
			var parentOffset = $(this).offset(),
				relX = e.pageX - parentOffset.left,
				relY = e.pageY - parentOffset.top;
			$(this).find("span").css({ top: relY, left: relX });
		})
		.on("mouseout", function (e) {
			var parentOffset = $(this).offset(),
				relX = e.pageX - parentOffset.left,
				relY = e.pageY - parentOffset.top;
			$(this).find("span").css({ top: relY, left: relX });
		});

	const cursor = document.querySelector(".cursor");

	document.addEventListener("mousemove", (e) => {
		cursor.style.setProperty("--x", e.clientX);
		cursor.style.setProperty("--y", e.clientY);
	});
});
