import "./Header.scss";
import { useState } from "react";
import { Link } from "react-router-dom";
import LoginModal from "./Modal/LoginModal";
function Header({ isUserState, tokenUtill, modifyMode, setModifyMode ,adminMode }) {
	
	function modifyModebtn(){
		if(modifyMode){
			return (<li href="#" className="nav-item" onClick={function(){setModifyMode(false)}}>
			Modify Mode Off
		</li>)
		}else{
			return (<li href="#" className="nav-item" onClick={function(){setModifyMode(true)}}>
			Modify Mode 
		</li>)
		}
	}

	function loginCheck() {
        console.log(adminMode)
        if(adminMode){
            if (isUserState) {
                return (
                    <>
                        {modifyModebtn()}
                        <li href="#" className="nav-item" onClick={tokenUtill.userLogOut}>
                            LOGOUT
                        </li>
                    </>
                );
            } else {
                return (
                    <li href="#" className="nav-item" onClick={showLMadol}>
                        LOGIN
                    </li>
                );
            }
        }
		
	}

	const [LModal, setLModal] = useState(false);
	const showLMadol = () => {
		setLModal(true);
	};

	return (
		<>
			<LoginModal LModal={LModal} setLModal={setLModal} tokenUtill={tokenUtill}></LoginModal>
			<div id="navbar">
				<div id="navbar-bg"></div>
				<div id="navbar-content">
					<a href="#" id="navbar-brand">
						<img src="/img/pluxitylogo_blue_edited_waifu2x_photo_noise1.png" />
						<span className="text">DTRC</span>
					</a>
					<ul id="navbar-menu">
						<li href="#" className="nav-item">
							WORKS
							<ul className="drop">
								<div>
									<li>
										<Link to={"3D"}>3D Rendering reaserch</Link>
									</li>
									<li>
										<Link to={"sensor"}>Sensor Data analysis</Link>
									</li>
									<li>
										<Link to={"Image"}>Image analysis</Link>
									</li>
								</div>
							</ul>
						</li>
						<li href="#" className="nav-item">
							ABOUT
							<ul className="drop">
								<div>
									<li>
										<Link to={"About"}>About Us</Link>
									</li>
									<li>
										<Link to={"Contact"}>Contact</Link>
									</li>
								</div>
							</ul>
						</li>

						{loginCheck()}
					</ul>
				</div>
			</div>
		</>
	);
}

export default Header;
