import React from "react";
import { Button } from "primereact/button";
import { useState } from "react";
import { Outlet } from "react-router-dom";

export default function () {
	const [isOpen, setOpen] = useState(false);
	function toggleSet() {

		if (isOpen) {
			return<> <Button  rounded   className="swiper-toggle toggle-open" onClick={function(){setOpen(false)}} icon="pi pi-chevron-down"></Button> </>;
		} else {
			return<> <Button  rounded   className="swiper-toggle toggle-close" onClick={function(){setOpen(true)}}  icon="pi pi-chevron-right"></Button></>;
		}
	}
	return <>{toggleSet()}</>;
}
